import '../scss/ress.scss';
import '../scss/style.scss';

import ScrollTrigger from './modules/scrollTrigger.js'

window.onload = () => {
  let trigger = new ScrollTrigger()

  let headH = document.getElementsByClassName('header').offsetHeight
  document.body.style.marginTop = headH + 'px'

  smoothLink(headH)
}

window.onscroll = () => {
  scrollClassToggler(150, '.header', 'header__scrolled')
}

function scrollClassToggler(offset, target, toggleClass) {
  if (window.pageYOffset > offset) {
    let el = document.querySelectorAll(target)[0]
    el.classList.add(toggleClass)
  } else {
    let el = document.querySelectorAll(target)[0]
    el.classList.remove(toggleClass)
  }
}

function smoothLink(headH = 0) {
  const interval = 10 //ã‚¹ã‚¯ãƒ­ãƒ¼ãƒ«å‡¦ç†ã‚’ç¹°ã‚Šè¿”ã™é–“éš”
  const divisor = 8 //è¿‘ã¥ãå‰²åˆï¼ˆæ•°å€¤ãŒå¤§ãã„ã»ã©ã‚†ã£ãã‚Šè¿‘ãï¼‰
  const range = divisor / 2 + 1 //ã©ã“ã¾ã§è¿‘ã¥ã‘ã°å‡¦ç†ã‚’çµ‚äº†ã™ã‚‹ã‹(ç„¡é™ãƒ«ãƒ¼ãƒ—ã«ãªã‚‰ãªã„ã‚ˆã†ã« divisor ã‹ã‚‰ç®—å‡º)
  const links = document.querySelectorAll('a[href^="#"]')

  for (let i = 0; i < links.length; i++) {
    links[i].addEventListener('click', function(e) {
      e.preventDefault()
      let toY
      let nowY = window.pageYOffset //ç¾åœ¨ã®ã‚¹ã‚¯ãƒ­ãƒ¼ãƒ«å€¤
      const href = e.target.getAttribute('href') //hrefå–å¾—
      const target = document.querySelector(href) //ãƒªãƒ³ã‚¯å…ˆã®è¦ç´ ï¼ˆã‚¿ãƒ¼ã‚²ãƒƒãƒˆï¼‰å–å¾—
      const targetRect = target.getBoundingClientRect() //ã‚¿ãƒ¼ã‚²ãƒƒãƒˆã®åº§æ¨™å–å¾—
      const targetY = targetRect.top + nowY - headH //ç¾åœ¨ã®ã‚¹ã‚¯ãƒ­ãƒ¼ãƒ«å€¤ & ãƒ˜ãƒƒãƒ€ãƒ¼ã®é«˜ã•ã‚’è¸ã¾ãˆãŸåº§æ¨™
      //ã‚¹ã‚¯ãƒ­ãƒ¼ãƒ«çµ‚äº†ã¾ã§ç¹°ã‚Šè¿”ã™å‡¦ç†
      ;(function doScroll() {
        toY = nowY + Math.round((targetY - nowY) / divisor) //æ¬¡ã«ç§»å‹•ã™ã‚‹å ´æ‰€ï¼ˆè¿‘ãå‰²åˆã¯é™¤æ•°ã«ã‚ˆã‚‹ã€‚ï¼‰
        window.scrollTo(0, toY) //ã‚¹ã‚¯ãƒ­ãƒ¼ãƒ«ã•ã›ã‚‹
        nowY = toY //nowYæ›´æ–°

        if (document.body.clientHeight - window.innerHeight < toY) {
          //æœ€ä¸‹éƒ¨ã«ã‚¹ã‚¯ãƒ­ãƒ¼ãƒ«ã—ã¦ã‚‚å¯¾è±¡ã¾ã§å±Šã‹ãªã„å ´åˆã¯ä¸‹é™ã¾ã§ã‚¹ã‚¯ãƒ­ãƒ¼ãƒ«ã—ã¦å¼·åˆ¶çµ‚äº†
          window.scrollTo(0, document.body.clientHeight)
          return
        }
        if (toY >= targetY + range || toY <= targetY - range) {
          //+-rangeã®ç¯„å›²å†…ã¸è¿‘ãã¾ã§ç¹°ã‚Šè¿”ã™
          window.setTimeout(doScroll, interval)
        } else {
          //+-range ã®ç¯„å›²å†…ã«ãã‚Œã°æ­£ç¢ºãªå€¤ã¸ç§»å‹•ã—ã¦çµ‚äº†ã€‚
          window.scrollTo(0, targetY)
        }
      })()
    })
  }
}
